<template>
  <div id="app">
    <!--<img src="./assets/logo.png">-->
    <transition mode="out-in" :duration="{enter:0,leave:0}" enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOutLeft">
        <router-view style='position: absolute;'></router-view>
    </transition>
    <tap-menu></tap-menu>
  </div>
</template>

<script>
  import tapMenu from './components/TapMenu.vue';
  export default {
    name: 'app',
    components:{
        tapMenu
    }
  }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
*{
  box-sizing: border-box;
}
[data-dpr = "1"] * {
  font-size: 12px!important;
}

[data-dpr = "2"] * {
  font-size: 24px!important;;
}

[data-dpr = "3"] * {
  font-size: 36px!important;;
}
</style>



