<template>
  <div id="tapMenu">
    <mt-tabbar class='tabbar' v-model="selected">

      <mt-tab-item id="wm" class="mint-tab-item">
          <img slot="icon" src="../assets/email.jpg"/>
          <span>外卖</span>
      </mt-tab-item>
      <mt-tab-item id="order" class="mint-tab-item">

          <img slot="icon" src="../assets/email.jpg"/>
          <span>订单</span>

      </mt-tab-item>
      <mt-tab-item id="find" class="mint-tab-item">

          <img slot="icon" src="../assets/email.jpg"/>
          <span>发现</span>

      </mt-tab-item>
      <mt-tab-item id="mine" class="mint-tab-item">

        <span>我的</span>
        <img slot="icon" src="../assets/email.jpg"/>
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>

<script>
  import bus from '../vuex/bus.js';
  export default {
    name: 'tapMenu',
    data(){
      return {
        selected: 'wm',
      }
    },
    watch: {
      selected(val) {
          this.$router.push(val);
      },
    },
    mounted(){

//        console.log(this.$router.history.current);
//        console.log(this.$router.history.current.fullPath.split('/')[1])
        this.selected = this.$router.history.current.fullPath.split('/')[1];
//        console.log(this.$router);333
    }
  };
</script>
<!--<style src="../../static/css/tabMenu.css" scoped></style>-->
<style >

  [data-dpr = "1"] * {
    font-size: 12px;
  }

  [data-dpr = "2"] * {
    font-size: 24px;
  }

  [data-dpr = "3"] * {
    font-size: 36px;
  }







  .tabbar {
    width: 100%;
    height: 1.5rem;
  }

  .mint-tab-item {
    display: flex!important;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

  }

  .mint-tab-item:hover{
    text-decoration: none!important;
  }

  .mint-tab-item-icon,
  .mint-tab-item-icon>img{
    width: .8rem!important;
    height: .8rem!important;
  }

  .is-selected{
    color:#000;
    text-underline: none!important;
  }

</style>



