<template>
  <div class="o-wrapper">
    <ul class="o-ul">
      <li class="o-li" v-for="(item,key) in resultList" :id="'li-'+key">{{item}}</li>
    </ul>
  </div>
</template>
<script>
//  import searchBar from '../search/search.vue';
  require('intersection-observer');
  export default {
    name:'order',
    data(){
      return {
        name:'order.vue',
        resultList:[1,2,3,4,5,6,7,8,9,0,11,12,13,14,15,1,2,3,4,5,6,7,8,9,0,11,12,13,14,15],
      }
    },
    methods:{

    },
    mounted(){
      var oWrapper = document.getElementsByClassName('o-wrapper')[0];
      oWrapper.style.position='static';
      var el4 = document.getElementById('li-4');
      var el5 = document.getElementById('li-5');
      var io = new IntersectionObserver(
        entries => {
          var t = entries[0].target;


          for(var i in entries){
              var ei = entries[i];
              if(ei.target===el4 && ei.intersectionRatio>0){
                  el5.style.position = 'static';
                  el5.style.background = '#fff';
              }
              if(ei.target == el5 && ei.intersectionRatio<1){
                  el5.style.position = 'fixed';
                  el5.style.top = '0';
                  el5.style.left = '0';
                  el5.style.background = '#ccc';
              }
          }
        },
        {
          threshold: [0.01,0.99]
        }
      );
      io.observe(el4);
      io.observe(el5);
    },
    beforeDestroy(){
      console.log('beforeDestroy');
    },
//    components:{searchBar}
  }
</script>
<style>
  .o-wrapper{
    width:100%;
    height: calc(100vh - 1.5rem);
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }
  .o-ul{
    list-style: none;
    margin:0;
    /*padding:.2rem;*/
  }
  .o-li{
    width: calc(100% - 0.0rem);
    height:1rem;
    padding: .1rem;
    border-bottom: 1px solid black;
    vertical-align: middle;
    line-height:1rem;
  }


</style>



